import React from 'react'
//import PropTypes from 'prop-types'

const StudentTimeBlock = props => {
    //console.log(props);
    return (
          
        <div className = "block" >

            <div className= "subBlock" style ={{display:'flex', }}>
                    <div className ="indBlock" onClick ={() => {console.log(props.id[0], props.time,props.studentName)}}>:00 </div>
                    <div className ="indBlock" onClick ={() => {console.log(props.id[15],props.time,props.studentName)}}>:15 </div>
                    <div className ="indBlock" onClick ={() => {console.log(props.id[30],props.time,props.studentName)}}>:30 </div>
                    <div onClick ={() => {console.log(props.id[45],props.time,props.studentName)}}>:45 </div>
            </div>
            
        </div>
           
             //       <div className ="StdindBlock" onClick ={
               //() => {console.log(props.id+" 00")}}> 0</div>
                    
            
     
    )
}


export default StudentTimeBlock
